import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import {
  Menu,
  Spin,
  message,
  Popconfirm,
  Form,
  Modal,
  Input,
  Tooltip,
  Button,
} from "antd";
import {
  FilePdfOutlined,
  PlusOutlined,
  DeleteOutlined,
  EditOutlined,
  EyeOutlined,
  FullscreenOutlined,
} from "@ant-design/icons";
import { BsChatLeftDots } from "react-icons/bs";

import { getSources } from "../../../../redux/source/sourceSlice";
import {
  deleteDocument,
  getDocuments,
  updateDocument,
} from "../../../../redux/document/documentSlice";
import { getStorage, getItem } from "../../../../helpers";
import { setSelectedSource } from "../../../../redux/app/appSlice";
import DocumentUploadModal from "../Modals/DocumentUploadModal";
import PDFviewModal from "./partials/PDFviewModal";

const SourceList = () => {
  // useEffect(() => { pdfjs.GlobalWorkerOptions.workerSrc = `pdf.worker.js`; });
  const [selectedDocument, setSelectedDocument] = useState({});
  const [openPDFModal, setOpenPDFModal] = useState(false);
  const loading = useSelector((state) => state.source.loading);
  const error = useSelector((state) => state.source.error);
  const sources = useSelector((state) => state.source.sources);
  const plan = useSelector((state) => state.auth.plan);
  const documents = useSelector((state) => state.document.documents);
  const uploading = useSelector((state) => state.document.loading);
  const dispatch = useDispatch();
  const selectedSource = useSelector((state) => state.app.selectedSource);
  const [openDocumentUploadModal, setOpenDocumentUploadModal] = useState(false);
  const [openDocumentUpdateModal, setOpenDocumentUpdateModal] = useState(false);
  const [form] = Form.useForm();
  const { id } = useParams();

  const confirm = (document) => {
    if(id) {
      return message.warning(
        "Can't manage this action."
      );
    }
    dispatch(deleteDocument(document._id));
  };
  const cancel = (e) => {
    console.log(e);
  };

  const items = sources.map(({ name, sourceId }, index) => {
    const docs = documents.filter((item) => item.sourceId === sourceId);
    const subMenu = docs.map((document) =>
      getItem(
        <div className="">
          <div
            className="max-w-full text-ellipsis overflow-hidden"
            title={document.name}
          >
            <FilePdfOutlined className="mr-1" /> {document.name}
          </div>
          <div className="flex justify-around">
            {" "}
            <Button
              type="primary"
              size="small"
              icon={<EyeOutlined />}
              onClick={() => {
                // window.open(selectedDocument.path.replace("public/", ""));
                setSelectedDocument(document);
                setOpenPDFModal(true);
              }}
            >
              View
            </Button>{" "}
            <Button
              type="primary"
              size="small"
              icon={<EditOutlined />}
              onClick={() => {
                if(id) {
                  return message.warning(
                    "Can't manage this action."
                  );
                }
                setSelectedDocument(document);
                setOpenDocumentUpdateModal(true);
              }}
            >
              Rename
            </Button>{" "}
            <Popconfirm
              title="Delete the document!"
              description="Are you sure to delete this document?"
              onConfirm={() => confirm(document)}
              onCancel={cancel}
              okText="Yes"
              cancelText="No"
            >
              <Button type="primary" size="small" icon={<DeleteOutlined />}>
                Delete
              </Button>
            </Popconfirm>{" "}
          </div>
          {/* <Dropdown
              onOpenChange={(open) => {
                if (open) {
                  setSelectedDocument(document);
                }
              }}
              trigger="click"
              menu={{ items: actionItems }}
              placement="bottomLeft"
              arrow={{ pointAtCenter: true }}
            >
              <div>
                <BsThreeDotsVertical />
              </div>
            </Dropdown> */}
          {/* <div></div> */}
        </div>,
        `${document._id}`,
        //   <FilePdfOutlined />,
        null,
        null,
        true
      )
    );
    subMenu.push(
      getItem(
        uploading == sourceId ? "Processing..." : "Add Document",
        `addDocument-${index}-${sourceId}-${docs.length}`,
        uploading == sourceId ? <Spin size="small" /> : <PlusOutlined />,
        null
      )
    );
    if (sourceId === "loading") {
      return getItem(
        name,
        `${sourceId}`,
        <Spin size="small" />,
        null,
        null,
        true
      );
    }

    if (subMenu.length > 0) {
      return getItem(name, `${sourceId}`, <BsChatLeftDots />, subMenu);
    }

    return getItem(name, `${sourceId}`, <BsChatLeftDots />);
  });

  useEffect(() => {
    dispatch(getSources({ id }));
    dispatch(getDocuments({ id }));
  }, []);

  useEffect(() => {
    let latestKey = getStorage("latestKey") || sources[0]?.sourceId;
    if (latestKey) {
      dispatch(setSelectedSource({ sourceId: latestKey }));
    }
  }, [sources]);

  // useEffect(() => {
  //     if (selectedSource) {

  //     }
  // }, [selectedSource]);

  if (loading)
    return <div className="text-center text-gray-400">Loading...</div>;

  const onOpenChange = (keys) => {
    if (keys.length < 2) return;
    let key = keys[keys.length - 1];
    dispatch(setSelectedSource({ sourceId: key }));
  };

  const handleClick = ({ key }) => {
    if (key.startsWith("addDocument")) {
      if (id) {
        return message.warning("Can't manage this action.");
      }
      if (uploading) {
        return message.warning("Server is busy now. Please wait a moment...");
      }

      if (key.split("-")[key.split("-").length - 1] >= plan.document) {
        return message.warning("You have reached maxiumn count of documents");
      }
      setOpenDocumentUploadModal(true);
    }
  };

  return (
    <div>
      <Menu
        className="sidebar"
        openKeys={[`${selectedSource}`]}
        onOpenChange={onOpenChange}
        mode="inline"
        theme="dark"
        items={items}
        selectable={false}
        onClick={handleClick}
      />
      <DocumentUploadModal
        open={openDocumentUploadModal}
        onClose={() => setOpenDocumentUploadModal(false)}
      />
      <Modal
        open={openDocumentUpdateModal}
        title="Change Documents"
        okText="Submit"
        cancelText="Cancel"
        onCancel={() => setOpenDocumentUpdateModal(false)}
        onOk={() => {
          form
            .validateFields()
            .then(async (values) => {
              dispatch(updateDocument({ ...values, id: selectedDocument._id }));
              form.resetFields();
              setOpenDocumentUpdateModal(false);
            })
            .catch((info) => {
              console.log("Validate Failed:", info);
            });
        }}
      >
        <Form form={form} layout="vertical">
          <Form.Item
            name="documentName"
            label="Document Name"
            initialValue={selectedDocument.name}
            rules={[
              {
                required: true,
                message: "Please input the name of property",
              },
            ]}
          >
            <Input />
          </Form.Item>
        </Form>
      </Modal>
      <PDFviewModal
        openPDFModal={openPDFModal}
        setOpenPDFModal={setOpenPDFModal}
        selectedDocument={selectedDocument}
      />
    </div>
  );
};

export default SourceList;
