import React from "react";

import { Modal, Tooltip } from "antd";
import { FullscreenOutlined } from "@ant-design/icons";

import { Viewer } from "@react-pdf-viewer/core";
import { fullScreenPlugin } from "@react-pdf-viewer/full-screen";
import { pageNavigationPlugin } from "@react-pdf-viewer/page-navigation";
import { zoomPlugin } from "@react-pdf-viewer/zoom";
import { searchPlugin } from "@react-pdf-viewer/search";
// Import styles
import "@react-pdf-viewer/core/lib/styles/index.css";
import "@react-pdf-viewer/full-screen/lib/styles/index.css";
import "@react-pdf-viewer/page-navigation/lib/styles/index.css";
import "@react-pdf-viewer/zoom/lib/styles/index.css";
import "@react-pdf-viewer/search/lib/styles/index.css";

const PDFviewModal = ({ selectedDocument, openPDFModal, setOpenPDFModal }) => {
  const fullScreenPluginInstance = fullScreenPlugin();
  const pageNavigationPluginInstance = pageNavigationPlugin();
  const zoomPluginInstance = zoomPlugin();
  const searchPluginInstance = searchPlugin({
    keyword: "",
  });
  const { EnterFullScreen } = fullScreenPluginInstance;
  const { CurrentPageLabel } = pageNavigationPluginInstance;
  const { ZoomInButton, ZoomOutButton, ZoomPopover } = zoomPluginInstance;
  const { ShowSearchPopoverButton } = searchPluginInstance;

  return (
    <Modal
      width={650}
      open={openPDFModal}
      title={selectedDocument.name}
      onCancel={() => setOpenPDFModal(false)}
      footer={[]}
    >
      <div className="flex justify-between items-center h-[32px] mx-4">
        <div className="flex-1">
          <div className="flex"><ShowSearchPopoverButton/> <small className="mt-1">Type Ctl+F for search</small></div>
        </div>
        <CurrentPageLabel>
          {(props) => (
            <span>{`${props.currentPage + 1} / ${props.numberOfPages}`}</span>
          )}
        </CurrentPageLabel>
        <div className="flex-1 flex items-center justify-end">
          <EnterFullScreen>
            {(props) => (
              <Tooltip placement="top" title={"Full Screen Mode"}>
                <span className="cursor-pointer mr-2" onClick={props.onClick}>
                  <FullscreenOutlined />
                </span>
              </Tooltip>
            )}
          </EnterFullScreen>
          <ZoomPopover />
          <ZoomInButton />
          <ZoomOutButton />
        </div>
      </div>
      <div
        style={{
          border: "0px solid rgba(0, 0, 0, 0.3)",
          height: "70vh",
        }}
      >
        {selectedDocument.path && (
          <Viewer
            fileUrl={selectedDocument.path.replace("public", "")}
            plugins={[
              fullScreenPluginInstance,
              pageNavigationPluginInstance,
              zoomPluginInstance,
              searchPluginInstance,
            ]}
          />
        )}
      </div>
    </Modal>
  );
};

export default PDFviewModal;
