import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { Layout } from "antd";
import Main from "./Main";
import SideBar from "./SideBar";
import SourceUploader from "./Modals/SourceUploader";
import { getPlan } from "../../../redux/auth/authSlice";
const { Content, Sider } = Layout;

function Home() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const plan = useSelector((state) => state.auth.plan);
  const { id } = useParams();

  useEffect(() => {
    dispatch(getPlan());
  }, []);

  useEffect(() => {
    console.log(id, plan);
    if(!plan && !id) {
        navigate('/plans');
    }
  }, [plan]);

  return (
    <>
      <Sider
        theme={"dark"}
        width={300}
        className="hidden sm:block z-50"
        style={{
          overflow: "auto",
          height: "100vh",
          position: "fixed",
          left: 0,
          top: 0,
          bottom: 0,
        }}
      >
        <SourceUploader />
        <SideBar />
      </Sider>
      <Layout className="sm:ml-[300px]">
        <Content>
          <Main />
        </Content>
      </Layout>
    </>
  );
}

export default Home;
