import { useEffect, useRef, useState } from "react";
import classNames from "classnames";
import { useLocation, useParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { Button, Empty, Spin, message } from "antd";
import $ from "jquery";

import { getMessages } from "../../../../redux/message/messageSlice";

// components
import ToolBar from "./ToolBar";
import PromptBox from "./PromptBox";
import MessageList from "./MessageList";
import { VerticalAlignBottomOutlined } from "@ant-design/icons";

const Main = () => {
  const messagesRef = useRef(null);
  const [ShowBottomToScroll, setShowBottomToScroll] = useState(false);
  const location = useLocation();
  const dispatch = useDispatch();
  const loading = useSelector((state) => state.message.loading);
  const error = useSelector((state) => state.message.error);
  const messages = useSelector((state) => state.message.messages || []);
  const selectedSource = useSelector((state) => state.app.selectedSource);
  const { id } = useParams();
  useEffect(() => {
    if (selectedSource) {
      dispatch(getMessages({ sourceId: selectedSource, id}));
    }
  }, [selectedSource]);

  useEffect(() => {
    $(".messagesRef").scrollTop($(".messagesRef").prop('scrollHeight'));
    if ($(".messageListRef").height() < $(window).innerHeight()) {
      setShowBottomToScroll(false);
    }
  }, [messages]);

  useEffect(() => {
    const handleScroll = (e) => {
      e.preventDefault();
      window.scrollTo({
        top: 0,
        left: 0,
        behavior: "instant",
      });
    };

    window.addEventListener('scroll', handleScroll, { passive: false });

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);


  useEffect(() => {
    $(".messagesRef").on("scroll", (e) => {
      if (e.target.scrollTop + e.target.clientHeight >= e.target.scrollHeight) {
        setShowBottomToScroll(false);
      } else {
        setShowBottomToScroll(true);
      }
    });
    $(".messageContainer").css("height", document.documentElement.clientHeight);
    $(window).on("resize", () => {
      $(".messageContainer").css("height", window.innerHeight);
    });
  }, []);

  return (
    <div className="max-w-full mx-auto">
      <div
        className={classNames(
          "bg-white flex flex-col h-screen max-h-[-webkit-fill-available] messageContainer"
        )}
      >
        <ToolBar />
        <div
          ref={messagesRef}
          className={classNames(
            "messagesRef scroll-smooth flex justify-center overflow-auto flex-1",
            {
              "items-center": loading || !messages.length,
            }
          )}
        >
          {loading ? (
            <Spin className="mt-4" size="large"></Spin>
          ) : messages.length > 0 ? (
            <MessageList messages={messages} />
          ) : (
            <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
          )}
        </div>
        <PromptBox />
        {ShowBottomToScroll && (
          <div className="absolute right-20 bottom-20 animate-bounce">
            <Button
              type="primary"
              shape="circle"
              icon={<VerticalAlignBottomOutlined />}
              onClick={() => {
                messagesRef.current.scrollTop =
                  messagesRef.current.scrollHeight;
              }}
            />
          </div>
        )}
      </div>
    </div>
  );
};

export default Main;
