import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import { Card, Drawer, Space, Button } from "antd";
import { VerticalAlignBottomOutlined } from "@ant-design/icons";

import LoadingMessage from "../../../../components/LoadingMessage";
import Message from "./Message";

const MessageList = ({ messages }) => {
  const messageListRef = useRef(null);
  const messageRef = useRef(null);
  const waiting = useSelector((state) => state.message.waiting);
  const selectedSource = useSelector((state) => state.app.selectedSource);
  const user = useSelector((state) => state.auth.user);
  const [open, setOpen] = useState(false);
  const [selMessageSource, setSelMessageSource] = useState([]);

  const showDrawer = (message) => {
    if (!user.isAdmin) return;
    setSelMessageSource(message.sourceDocuments);
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    //  scroll to bottom every time messages change
    setTimeout(() => {
      // messageListRef.current?.scrollIntoView({ behavior: "smooth" });
    }, 10);
  }, [messages]);
  let i = 0;
  const onType = (e) => {
    i++;
    if (i % 25 === 0) {
      messageListRef.current?.scrollIntoView({ behavior: "smooth" });
    }
  };

  return (
    <>
      <div
        ref={messageRef}
        className="w-full h-fit max-w-3xl mx-auto messageListRef pl-[8px]"
      >
        {selectedSource &&
          messages.map((message, index) => (
            <Message
              onType={onType}
              key={index}
              message={message}
              isLast={messages.length === index + 1}
              onClick={() => showDrawer(message)}
            />
          ))}
        {waiting && <LoadingMessage />}
        <div ref={messageListRef} className="scrollBottomRef"></div>
        <Drawer
          title="Sources Documents"
          width={350}
          placement="right"
          onClose={onClose}
          open={open}
        >
          <Space direction="vertical">
            {selMessageSource.map((source, i) => (
              <Card title={`Document ${i + 1}`} key={i}>
                <p>{source.pageContent}</p>
              </Card>
            ))}
          </Space>
        </Drawer>
      </div>
    </>
  );
};

export default MessageList;
